export default class extends React.Component {

  static async getInitialProps({ query }) {
    let idPodcast = query.id

    let reqPodcast = await fetch(`https://api.audioboom.com/audio_clips/${idPodcast}.mp3`)
    let dataPodcast = await reqPodcast.json()
    let podcast = dataPodcast.body.audio_clip
    return { podcast }
  }

  render() {
    let { podcast } = this.props
    return <div className="container">
      <div className="cover">
        <img src={podcast.channel.urls.logo_image.original} alt={podcast.title}/>
      </div>

      <div className="title">
        <h2>{podcast.channel.title}</h2>
      </div>

      <style jsx>{`
        .container {
          display: flex;
          justify-content: center;
        }
        .cover img {
          width: 300px;
        }
        `}
      </style>
    </div>
  }
}


